body {
    margin: 0;
    padding: 0;
    font-family: "Neucha", cursive;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

/* my color palette variables */

:root {
    --text-color: #222f3e;
    --section-color: #54a0ff;
    --border-color: #c8d6e5;
    --custom-red: #ff6b6b;
}

/* basic reset */

* {
    box-sizing: border-box;
}

html,
body {
    margin: 0;
    padding: 0;
}

html {
    font-size: 14px;
}

body {
    font-weight: normal;
    font-style: normal;
    color: #222f3e;
    color: var(--text-color);
}

ul {
    padding: 0;
    margin: 0;
}

li {
    list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
figure {
    margin: 0;
}

section {
    font-size: 1rem;
}

a {
    color: #222f3e;
    color: var(--text-color);
    text-decoration: none;
}

a:active {
    text-decoration: none;
}

a:active,
a:focus,
a:visited {
    color: #222f3e;
    color: var(--text-color);
}

img {
    max-width: 100%;
    height: auto;
}

p {
    line-height: 1.5;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
}

.row {
    display: -webkit-flex;
    display: flex;
}

.filter-panel {
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

.characters-panel {
    -webkit-flex-grow: 3;
            flex-grow: 3;
}

.character-item {
    text-align: center;
    -webkit-flex: 0 1 33.33%;
            flex: 0 1 33.33%;
}

.character-item__title {
    font-size: 1.5em;
    margin-bottom: 1.5rem;
}

.item-thumb-wrap {
    width: 128px;
    height: 128px;
    margin: 0 auto 1em;
    overflow: hidden;
}

.item-thumb-image {
    border-radius: 50%;
}

.thumb-male .item-thumb-image {
    border: 2px solid var(--section-color);
}

.thumb-female .item-thumb-image {
    border: 2px solid var(--custom-red);
}

.characters-wrap {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

